import React, { useEffect, useState } from "react";
import API from "../../utils/API";
import Container from "react-bootstrap/Container";
import { Link, useParams } from "react-router-dom";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import ListGroup from "react-bootstrap/ListGroup";
import Card from "react-bootstrap/Card";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import "./style.css";

function TicketPage(props) {
  const [ticket, setTicket] = useState({});

  // When this component mounts, grab the book with the _id of props.match.params.id
  // e.g. localhost:3000/books/599dcb67f0f16317844583fc
  const { id } = useParams();
  useEffect(() => {
    API.getTicket(id)
      .then((res) => setTicket(res.data))
      .catch((err) => console.log(err));
  }, []);

  function statusStr(status) {
    if (status === 1) {
      return "New";
    } else if (status === 2) {
      return "In Progress";
    } else if (status === 3) {
      return "Closed";
    } else {
      return "Closed";
    }
  }

  return (
    <Container fluid>
      <Link to="/tickets"> Back To Tickets </Link>
      <Row>
        <Col>
          <Form>
            <Form.Group as={Col} controlId="formGridTitle">
              <Form.Label>Title</Form.Label>
              <Form.Control type="text" placeholder="title" />
            </Form.Group>

            <Form.Row>
              <Form.Group controlId="formGridPriority">
                <Form.Label>Priority</Form.Label>
                <Form.Control as="select" defaultValue="High">
                  <option>High</option>
                  <option>Medium</option>
                  <option>Low</option>
                </Form.Control>
              </Form.Group>

              <Form.Group controlId="formGridStatus">
                <Form.Label>Status</Form.Label>
                <Form.Control as="select" defaultValue="New">
                  <option>New</option>
                  <option>In Progress</option>
                  <option>Closed</option>
                </Form.Control>
              </Form.Group>
              <Form.Group as={Col} controlId="formGridBy">
                <Form.Label>Requested By</Form.Label>
                <Form.Control />
              </Form.Group>
            </Form.Row>
            <Form.Group as={Col} controlId="formGridDescription">
              <Form.Label>Description</Form.Label>
              <Form.Control type="text" placeholder="description" />
            </Form.Group>

            <Form.Row>
              <Form.Group as={Col} controlId="formGridCategory">
                <Form.Label>Category</Form.Label>
                <Form.Control as="select" defaultValue="General">
                  <option>General</option>
                  <option>Security</option>
                  <option>Software</option>
                  <option>Hardware</option>
                </Form.Control>
              </Form.Group>

              {/* <Form.Group as={Col} controlId="formGridSub">
                <Form.Label>Subcategory</Form.Label>
                <Form.Control />
              </Form.Group> */}
              <Form.Group as={Col} controlId="formGridComments">
                <Form.Label>Comments</Form.Label>
                <Form.Control />
              </Form.Group>
            </Form.Row>

            <Form.Group id="formGridCheckbox">
              <Form.Check type="checkbox" label="Check me out" />
            </Form.Group>

            <Button variant="primary" type="submit">
              Submit
            </Button>
          </Form>
        </Col>
      </Row>
      <Row>
        <Col sm={12}>
          <div className="scroll-div">
            <ListGroup className="ticket-list">
              <ListGroup.Item className="mycard-item" key={ticket._id}>
                <Card>
                  <Card.Body className="mycard">
                    <Row>
                      <Col>
                        <Card.Title className="mycard-title">
                          {ticket.title}
                        </Card.Title>
                      </Col>
                      <Col>
                        <Card.Text className="mycard-text aln-right">
                          {"Priority:  " + ticket.priority}
                        </Card.Text>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={8}>
                        <Card.Text>{ticket.description}</Card.Text>
                      </Col>
                    </Row>
                    <Row>
                      <Col>Category: {ticket.category}</Col>
                      <Col> {ticket.subCategory}</Col>
                      <Col className="aln-right">
                        Status: {statusStr(ticket.status)}
                      </Col>
                    </Row>
                  </Card.Body>
                </Card>
              </ListGroup.Item>
            </ListGroup>
          </div>
        </Col>
      </Row>
    </Container>
  );
}
export default TicketPage;
